body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.errorPage {
  background: url(https://i.ibb.co/sv6wRvq/wildwest-bg.jpg);
  background-size: cover;
  background-position: bottom;
  width: 100%;
  height: 100vh;
}

img.errorPage__tembleweed {
  animation: templeWeed 5s infinite 1s;
}

.errorPage__templeweed-container {
  position: absolute;
  bottom: 20px;
  width: 10vw;
  transform: translateX(-100%);
  animation: moveWeed 3s infinite linear;
}

.errorPage__text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  margin-top: -5%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.errorPage__button {
  display: block;
  cursor: pointer;
  border: 1px solid #fff;
  color: #e3594e;
  padding: 10px 30px;
  letter-spacing: 0.2em;
  box-shadow: 7px 7px 20px #00000026;
  background: #fff;
  transition: transform 300ms ease;
}

.errorPage__button:active {
  transform: translateY(2px);
  box-shadow: 3px 3px 20px #00000026;
}

.errorPage__text h3 {
  font-size: 20vw;
  line-height: 1;
  margin: 0;
  letter-spacing: 0.2em;
  margin-bottom: 50px;
  color: #fff;
  text-shadow: 7px 7px 20px #00000026;
}

