@keyframes moveWeed {
  0% {
      left: 0;  
      transform: translateX(-100%) rotate(0);
  }
  
  100% {
      left: 130%;
      transform: translateX(100%) rotate(540deg);
  }
}

@keyframes templeWeed {
  0% {
      transform: translateY(0);
  }
  
  30% {
      transform: translateY(-3vw);
  }
  
  60% {
      transform: translateY(-4vw);
  }
  
  90% {
      transform: translateY(0);
  }
  
  90% {
      transform: translateY(0);
  }
  
}
